.main {
    width: 100%;
    height: 100%;
    background-color: black;
    position: fixed;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
}

.main-container .image-glitch {
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.main-container .projects {
    position: fixed;
    z-index: 1;
    left: 50%;
    top: calc(50%);
    -webkit-transform: translate(-50%, -250px);
        -ms-transform: translate(-50%, -250px);
            transform: translate(-50%, -250px);
}

.main-container .contact {
    position: fixed;
    z-index: 1;
    left: 50%;
    top: calc(50%);
    -webkit-transform: translate(-50%, 210px);
        -ms-transform: translate(-50%, 210px);
            transform: translate(-50%, 210px);
}

.main-container .about {
    position: fixed;
    z-index: 10;
    left: calc(50% + 340px);
    top: 50%;
}
.main-container .about:hover{
    left: calc(50% + 180px);
}

.main-container .resume {
    position: fixed;
    z-index: 10;
    left: calc(50% - 465px);
    top: 50%;
}
.main-container .resume:hover{
    left: calc(50% - 635px);
}



