.menu-button .border-transition {
    background-color: black;
    -webkit-transition: background-color 0.5s linear 0.5s;
    -o-transition: background-color 0.5s linear 0.5s;
    transition: background-color 0.5s linear 0.5s;
}

.pdf-container .button {
    position: absolute;
    z-index: 2;
}
.menu-button .button-active::after {
    border-bottom-color: transparent;
}

.menu-button .button-menu-options {
    position: absolute;
    z-index: -1;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    background-color: black;
    border: none;
    -webkit-transition: all 0.25s;
    -o-transition: all 0.25s;
    transition: all 0.25s;
}
.menu-button .button-menu-options-active {
    border: 2px solid aqua;
}

.menu-button .button-menu-options-item {
    background: none;
    border: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: aqua;
    font-size: 14px;
    font-weight: 600;
}
.menu-button .button-menu-options-item:hover {
    background: white;
    color: black;
    cursor: pointer;
}



