.hex-background canvas {
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
}

.hex-background img {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    -o-object-fit: cover;
       object-fit: cover;
    z-index: 1;
}



